@import url(https://fonts.googleapis.com/css?family=Lato:300;700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  overflow: auto;
  font-family: 'Lato', sans-serif;
}

body {
  background-color: #222;
  color: #ddd;
}

h3 {
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2rem;
  padding: 10px 0;
}

p {
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5rem;
  padding: 10px 0;
}

.row .col b {
  font-weight: 700;
}

li {
  margin: 20px 10px;
  list-style: none;
}

a {
  font-size: 1.2rem;
  color: #a17e08;
  text-decoration: none;
}

a:hover {
  color: #d65f1a;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

a:active {
  color: #b61111;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

span {
  font-size: 1.3rem;
  text-align: right;
}

element {
  margin-top: 20px;
}

.container {
  max-width: 900px;
  margin: 20px auto;
  min-height: 300px;
  padding: 40px;
  word-spacing: 2px;
}

.logo {
  display: flex;
  justify-content: center;
  height: 100px;
  width: 90px;
  margin: 30px 0;
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.mobile {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-width: 350px;
}

.close {
  font-family: inherit;
  position: absolute;
  top: 15px;
  right: 60px;
  font-size: 0.8rem;
  background-color: transparent;
  color: #a17e08;
  padding: 5px 10px;
  border: 1px solid #a17e08;
  border-radius: 5px;
  cursor: pointer;
}

.close:hover,
.btn-primary:hover {
  background-color: #a17d0841;
  -webkit-transform: scale(0.98);
          transform: scale(0.98);
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.home img {
  max-width: 100%;
  border-radius: 3px;
}

.home_text {
  margin: 30px auto;
  padding: 10px;
  text-align: center;
}

.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  margin: 20px 0;
  padding: 10px 0;
}

.col {
  width: 50%;
}

img.col {
  width: 100%;
  max-width: 250px;
  height: auto;
  object-fit: contain;
  border-radius: 3px;
  margin: 10px;
}

p.col {
  margin: 10px;
}

.form-group {
  position: relative;
  /* border: 1px solid red; */
  max-width: 500px;
  margin: 10px auto;
  color: #a17e08;
}

.form-control {
  background-color: transparent;
  border: 1px solid #a17e08;
  color: #eee;
  margin: 5px 0;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  outline: none;
}

.btn-primary {
  background-color: transparent;
  border: 1px solid #a17e08;
  color: #a17e08;
  padding: 10px;
  border-radius: 10px;
  cursor: pointer;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 30px auto;
  color: #d65f1a;
}

.footer .nav a {
  padding: 5px 15px;
}

@media (max-width: 678px) {
  .about {
    flex-direction: column;
    flex: 1 1;
  }
  .col {
    width: 100%;
  }
  img.col {
    max-width: 300px;
    margin: 20px auto;
  }
  .footer {
    flex-direction: column;
    text-align: center;
    padding: 10px;
    line-height: 1.5;
  }
  .footer .nav {
    flex-wrap: wrap;
  }
}

